.content {
    display: flex;
    flex-direction: column;
    padding: 15px 27px;
    width: calc(100vw - 300px);
    background: #EFF1F9;

    &Card {
        width: calc(16.6% - 10px);
        height: 94px;
        padding: 13px 24px;
        margin-bottom: 10px;
        border-radius: 5px;
        background: #fff;
        color: #9FA2B4;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }

    &List {
        margin: 15px -15px 0 -15px;
        height: 100%;
    }

    &listveiw {
        display: flex;
        flex-direction: column;
        height: 100%;

        &_header {
            display: flex;
            align-self: flex-end;
            background: #fff;
            border-radius: 5px;
            padding: 3px 6px 2px;
        }

        &_tabs {
            display: flex;
        }
    }
}