.queues {
    display: flex;
    width: 100%;
    justify-content: space-between;
    height: 100%;

    &_name {
        width: 50%;
        min-height: 100%;
        background: #fff;
        border-radius: 8px;
        padding-top: 32px;
        padding-bottom: 50px;
        flex-grow: 1;
        margin-right: 20px;
        max-height: 320px;
        // overflow-y: auto;
    }

    &_data {
        width: 50%;
        min-height: 100%;
        background: #fff;
        border-radius: 8px;
        padding-top: 32px;
        padding-bottom: 50px;
        flex-grow: 1;
        max-height: 320px;
        // overflow-y: auto;
    }

    &_title {
        padding-left: 32px;
        padding-bottom: 14px;
        font-size: 22px;
        line-height: 24px;
        color: #252733;
        font-weight: bold;
    }

    &_list {
        list-style-type: none;
        overflow-y: auto;
        height: 95%;

        & li {
            padding: 20px 32px;
            cursor: pointer;
            display: flex;
            justify-content: space-between;

            &:hover {
                background: rgb(190, 191, 197);
                color: #fff;
            }
        }
    }
}