body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.card-wrap {
  width: calc(16.6% - 10px);
  height: 94px;
  padding: 13px 24px;
  margin-bottom: 10px;
  border-radius: 5px;
  background: #fff;
  color: #9FA2B4;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.wrap-red {
  animation: pulse 1.8s infinite;
}

.wrap-yellow {
  animation: pulse 1.8s infinite;
}

.wrap-green {
  animation: pulse 1.8s infinite;
}

.trigger-div {
  text-align: center;
}

.tooltip-container {
  background-color: #444444 !important;
  color: #fff;
}

.tooltip-arrow[data-placement*='right']::after {
  border-color: transparent #444 transparent transparent !important;
  border-width: 0.5rem 0.4rem 0.5rem 0;
  left: 6px;
  top: 0;
}

.tooltip-arrow[data-placement*='left']::after {
  border-color: transparent transparent transparent #444 !important;
  border-width: 0.5rem 0 0.5rem 0.4em;
  left: 3px;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(192, 24, 24, 0.4);
  }

  70% {
    box-shadow: 0 0 0 10px rgba(192, 24, 24, 0);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(192, 24, 24, 0);
  }
}

@keyframes pulseYellow {
  0% {
    box-shadow: 0 0 0 0 rgba(150, 189, 43, 0.4);
  }

  70% {
    box-shadow: 0 0 0 10px rgba(150, 189, 43, 0);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(150, 189, 43, 0);
  }
}

@keyframes pulseGreen {
  0% {
    box-shadow: 0 0 0 0 rgba(13, 148, 24, 0.4);
  }

  70% {
    box-shadow: 0 0 0 10px rgba(13, 148, 24, 0);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(13, 148, 24, 0);
  }
}