.sidebar {
    width: 240px;
    min-width: 300px;
    background: #363740;
    padding-top: 19px;

    &ItemClass {
        height: 56px;
        display: flex;
        align-items: center;
        color: #A4A6B3;
        padding-left: 32px;
        cursor: pointer;
        transition: .16s;

        &_active {
            border-left: 2px solid #DDE2FF;
            background-color: #9FA2B4;
            color: #DDE2FF;
        }

        &:hover {
            background: #9FA2B4;
            color: #333;
        }
    }

    &NavClass {
        display: flex;
        flex-direction: column;
    }
}