.table {
    background: #fff;
    border-radius: 5px;
    font-size: 20px;
    border-collapse: collapse;

    &_head {

        & td {
            padding: 13px 15px;
            text-align: center;
        }

        & td:first-child {
            padding: 13px 15px;
            text-align: left;
        }
    }

    &_body {
        & td {
            padding: 13px 15px;
            text-align: center;
        }

        & td:first-child {
            padding: 13px 15px;
            text-align: left;
        }

        & tr:nth-child(2n) {
            background: #EAECF9;
        }
    }
}